/**
* 双随机-监管对象组件
* @module 系统
* @desc 查看
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center top="50px"
                   width="80%">

            <table class="gridTable" v-loading="loading">
                <tr>
                    <td class="label">对象类型：</td>
                    <td colspan="4">
                        <span v-if="objArr.find(item=>{return item.dictValue == form.objectType})">{{objArr.find(item=>{return item.dictValue == form.objectType}).dictName}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">对象名称：</td>
                    <td colspan="4">
                        <span>{{form.objectName}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">联系电话：</td>
                    <td>
                        <span>{{form.objectTel}}</span>
                    </td>
                    <td class="label">是否有证：</td>
                    <td>
                        <span>{{form.isCertificates==0?'无证':"有证"}}</span>
                    </td>
                </tr>
                <tr>
                    <td class="label">对象地址：</td>
                    <td colspan="4">
                        <span>{{form.objectAddress}}</span>
                    </td>
                </tr>
                <!-- <tr>
                    <td>是否有证</td>
                    <td>
                        <span>{{form.isCertificates==0?'无证':"有证"}}</span>
                    </td>
                </tr> -->
                <tr>
                    <td class="label">检查单位：</td>
                    <td>
                        <span v-if="options.find(item=>{return item.orgId == form.checkUnit})">{{options.find(item=>{return item.orgId == form.checkUnit}).orgName}}</span>
                    </td>
                    <td class="label">检查频次：</td>
                    <td>
                        <span v-if="checkArr.find(item=>{return item.value == form.checkFreq})">{{checkArr.find(item=>{return item.value == form.checkFreq}).label}}</span>
                    </td>
                </tr>

                <tr>
                    <td class="label">备注信息：</td>
                    <td colspan="4">
                        <span>{{form.remarks}}</span>
                    </td>
                </tr>
            </table>


        </el-dialog>
    </div>
</template>
<script>
    import {apiSuperviseObjectGet} from "../../../api/case";
    import {apiCommonGetUsersByOrg, apiOrgSearch} from "../../../api/system";

    export default {
        name: 'edit',
        props: {
            editId: {
                type: String,
                default: ''
            },
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: 'normal'
            },
            type: {
                type: String,
                default: ''
            },
        },
        data() {

            return {
                loading: true,
                checkArr: [
                    {
                        value: 'month',
                        label: '每月检查'
                    },
                    {
                        value: 'quarter',
                        label: '每季度检查'
                    },
                    {
                        value: 'year',
                        label: '每年份检查'
                    }
                ],
                form: {},
                objArr: [],
                options: [],


            }
        },
        methods: {
            closeDialog() {
                this.$emit('update:show', false)
            },
            clearV() {
                this.$refs.ruleForm.clearValidate()
            },

            remoteMethod(data) {
                apiOrgSearch({keyWord: data}).then(res => {
                    this.options = res.data
                })
            },
            getUserListByOrg(data) {
                this.form.orgName = this.options.find(i => {
                    return i.orgId === data
                }).orgName;
                apiCommonGetUsersByOrg(data).then(res => {
                    this.userList = res.userList
                })
            },

            //弹框弹出时获取data给form
            initPage() {
                this.remoteMethod();
                //检查对象类型下拉
                this.$until.workBookApi('checkObjectType').then(res => {
                    this.objArr = res;
                });

                if (this.editId) {
                    //    编辑get接口
                    apiSuperviseObjectGet(this.editId).then(res => {
                        this.form = res.data;
                        this.form.isCertificates = String(this.form.isCertificates);
                        this.loading = false;
                    })
                } else {

                }
            }

        },
        //监听，如果show为true,则执行initPage()
        watch: {
            'show'(value) {
                if (value) {
                    this.initPage();

                }
            }
        },

    }
</script>
<style lang='less' scoped>
    table {
        width: 100%;
        border: 1px solid #ccc;

        tr {
            height: 50px;

            td {
                padding-left: 5px;
                border: 1px solid #ccc;
            }
        }
    }

    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;

    }

    .widthselect {
        width: 100%
    }

    .cssfont {
        font-weight: bold;
    }
</style>


