/**
* 双随机-监管对象组件
* @module 系统
* @desc 列表
* @author 乔亚军
* @date 2019-6-4
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from
                    :onAdd="onAdd"
                    :onDel="onDelAll"
                    :searchObj="searchObj"
                    @searchList="searchList"
            />
        </div>
        <!-- 表格 -->
        <el-table
                :data="list"
                :header-cell-style="{background:'#e9eff8'}"
                :row-style="{cursor:'pointer'}"
                @row-click="seeFunction"
                @selection-change="handleSelectionChange"
                border
                size="mini"
                v-loading="loading"
        >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column align="center" label="对象名称" prop="objectName" show-overflow-tooltip/>
            <el-table-column align="center" label="类别" prop="objectType" show-overflow-tooltip>
                <template
                        slot-scope="scope"
                        v-if="scope.row.objectType"
                >{{scope.row.objectType | objectType}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="是否有证" prop="isCertificates" show-overflow-tooltip>
                <template slot-scope="scope">{{scope.row.isCertificates | isCertificates}}</template>
            </el-table-column>
            <el-table-column align="center" label="检查单位" prop="checkUnit" show-overflow-tooltip>
                <template slot-scope="scope" v-if="scope.row.checkUnit">{{scope.row.checkUnit | checkUnit}}</template>
            </el-table-column>
            <el-table-column
                    align="center"
                    label="联系电话"
                    prop="objectTel"
                    show-overflow-tooltip
                    width="150"
            />
            <!-- <el-table-column align="center" label="检查地址" prop="objectAddress" show-overflow-tooltip/> -->
            <!-- <el-table-column align="center" label="备注信息" prop="remarks" show-overflow-tooltip/> -->

            <el-table-column align="center" label="操作" width="130">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="seeFunction(scope.row)"
                                class="el-icon-view btn-link"
                                type="success"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="编辑" effect="dark" placement="top-end">
                        <el-link @click.stop="editFunction(scope.row)" icon="el-icon-edit" type="primary"></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="删除" effect="dark" placement="top-end">
                        <el-link
                                @click.stop="delateFunction(scope.row)"
                                class="el-icon-delete btn-link"
                                type="danger"
                        >
                            <i></i>
                        </el-link>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <lj-pagination
                :pageNum.sync="form.pageNum"
                :pageSize.sync="form.pageSize"
                :total="total"
                @getData="getList"
                class="page-css"
        />

        <!-- 编辑&添加弹框 -->
        <change-edit
                :editId="editObj.editId"
                :show.sync="editObj.show"
                :title="editObj.title"
                :type="editObj.type"
                @getList="getList"
                ref="child"
        />
        <!-- 查看 -->
        <see-content
                :editId="seeObj.editId"
                :show.sync="seeObj.show"
                :title="seeObj.title"
                :type="seeObj.type"
                ref="child"
        />
    </div>
</template>

<script>
    import {apiSuperviseObjecdeleteBatch, apiSuperviseObjectSearch} from "../../../api/case";
    import {apiOrgSearch} from "../../../api/system";
    import LjPagination from "../../../components/LjPagination";
    import LjSearchFrom from "../../../components/LjSearchForm";
    import {formatDate} from "../../../until/wordBook";
    import changeEdit from "./editadd";
    import seeContent from "./seecontent";

    let that;
    export default {
        name: "superviseObjPage",
        data() {
            return {
                //弹出框数据
                editObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null
                },
                seeObj: {
                    show: false,
                    title: "",
                    type: "",
                    editId: null
                },
                objArr: [],
                multipleSelection: [],
                //搜索组件数据
                searchObj: null,
                options: [],
                loading: true,
                form: {
                    pageSize: 20,
                    pageNum: 1
                },
                total: 0,
                input: "",
                list: [
                    {
                        scId: "aaa1112222sss3ddd",
                        crimeTime: "2019-01-20",
                        crimeLocal: "XXX",
                        caseSource: "sssss",
                        HandlingUnit: "aa",
                        investigatorsName: "aa",
                        party: "aa",
                        partyType: "aa",
                        correctionMethod: "aa",
                        correctionResult: "aa",
                        caseStatus: "aa"
                    }
                ],
                rowdata: {}
            };
        },
        components: {
            changeEdit,
            LjPagination,
            LjSearchFrom,
            seeContent
        },

        methods: {
            //查询+搜索
            getList() {
                this.loading = true;
                apiSuperviseObjectSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },

            //新增账号
            onAdd() {
                this.editObj.type = "2";
                this.editObj.title = "新增监管对象";
                this.editObj.show = true;
                this.editObj.editId = null;
            },
            seeFunction(row) {
                this.seeObj.title = "监管对象查看";
                this.seeObj.show = true;
                this.seeObj.editId = row.objId;
            },

            //编辑
            editFunction(row) {
                this.editObj.title = "修改监管对象信息";
                this.editObj.editId = row.objId;
                this.editObj.show = true;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            //批量删除
            onDelAll() {
                this.$confirm(
                    "此操作将删除" + this.multipleSelection.length + "条数据, 是否继续?",
                    "提示",
                    {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning"
                    }
                ).then(() => {
                    let arr = [];
                    this.multipleSelection.forEach(i => {
                        arr.push(i.objId);
                    });
                    apiSuperviseObjecdeleteBatch({objIds: arr.join(",")}).then(() => {
                        this.getList();
                        this.$message({
                            type: "success",
                            message: "批量删除成功!"
                        });
                    });
                });
            },
            //搜索
            searchList(data) {
                //data就是接收的this.form数据
                this.loading = true;
                data.pageSize = this.form.pageSize;
                this.form.pageNum = 1;
                apiSuperviseObjectSearch(data).then(res => {
                    this.list = res.data;
                    this.total = res.total;
                    this.loading = false;
                });
            }
        },
        beforeCreate: function () {
            that = this;
        },

        filters: {
            isCertificates(val) {
                return val == 0 ? "无证" : val == 1 ? "有证" : "暂无状态";
            },
            formatDate(value) {
                return formatDate(value, "yyyy-MM-dd");
            },
            checkUnit(val) {
                if (
                    that.options.find(item => {
                        return item.orgId == val;
                    })
                ) {
                    return that.options.find(item => {
                        return item.orgId == val;
                    }).orgName;
                }
            },
            objectType(val) {
                if (
                    that.objArr.find(item => {
                        return item.dictValue == val;
                    })
                ) {
                    return that.objArr.find(item => {
                        return item.dictValue == val;
                    }).dictName;
                }
            }
        },
        mounted() {
        },

        created() {
            this.getList();
            apiOrgSearch({keyWord: ""}).then(res => {
                this.options = res.data;
            });
            //检查对象类型下拉
            this.$until.workBookApi("checkObjectType").then(res => {
                this.objArr = res;
            });
        }
    };
</script>

<style scoped>
    .wrop-search {
        margin-bottom: 10px;
    }

    .btn-link {
        margin: 0 10px;
    }

    .page-css {
        margin-top: 10px;
    }
</style>
